import React, { Component } from "react";
import "../css/app.css";
//import ScatterBridge from "./scatterBridge";
import ScatterJS from 'scatterjs-core';
import ScatterEOS from 'scatterjs-plugin-eosjs';
import Eos from 'eosjs';

class App extends Component {
  constructor(props) {
    super(props);

    this.network = {
      blockchain: "eos",
      protocol: "https",
      host: "api.eoseoul.io",
      port: 443,
      chainId: "aca376f206b8fc25a6ed44dbdc66547c36c6c33e3a119ffbeaef943642f0e906"
    };

    //this.scatter = new ScatterBridge(this.network, "EOS Knight Event");
    this.onClick = this.onClick.bind(this);
  }

  async onClick() {

    ScatterJS.plugins( new ScatterEOS() );

    const network = ScatterJS.Network.fromJson({
        blockchain:'eos',
        chainId:'aca376f206b8fc25a6ed44dbdc66547c36c6c33e3a119ffbeaef943642f0e906',
        host:'nodes.get-scatter.com',
        port:443,
        protocol:'https'
    });

    ScatterJS.connect('YourAppName', {network}).then(connected => {
        if(!connected) return console.error('no scatter');

        const eos = ScatterJS.eos(network, Eos);

        ScatterJS.login().then(id => {
            if(!id) return console.error('no identity');
            const account = ScatterJS.account('eos');
            const options = {authorization:[`${account.name}@${account.authority}`]};
            eos.transfer(account.name, 'safetransfer', '0.0001 EOS', account.name, options).then(res => {
                console.log('sent: ', res);
            }).catch(err => {
                console.error('error: ', err);
            });
        });
    });



    /*
    const scatter = this.scatter;
    await scatter.connect();
    await scatter.login();
    await scatter.loadContract("eosknightsio");
    await scatter.contract.signup("raindaysmain");
    */
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <button onClick={this.onClick}>Test</button>
        </header>
      </div>
    );
  }
}

export default App;
